        h2 1.2 El arquitecto de la informacin
      .row
        p.mb-5 Para generar un plan de pruebas de usabilidad a cualquier interfaz de un sistema <i>software</i> interactivo, es necesario conocer de manera conceptual y epistemolgica diversos conceptos y reas que de por s generarn mayor autonoma a la hora de aplicar una tcnica de evaluacin de usabilidad. Es por eso que en este apartado se contina con trminos y frmulas claves para el aprendizaje efectivo de esta disciplina.

