          p.mb-5 Para el proceso de evaluacin de una interfaz, no solo basta con revisar la naturaleza funcional de la aplicacin, sino que es necesario entender su concepto mismo, a lo que est destinada, es decir, cul es la razn de esa aplicacin, a qu tipo de usuario objetivo est destinada y las caractersticas especiales derivadas de los requerimientos, como puede observarse en la Figura 1. Al respecto, se presenta una serie de pasos que dar pistas al <i>tester</i> para entender la lgica de la interfaz:

