                  p Para decidir qu incluir, se debe soportar en la investigacin y el conocimiento que se haya obtenido sobre los usuarios; esta informacin debe ser realizada por instancia del equipo de desarrollo de la empresa, pero tambin sobre el sitio <i>web</i> o app actual (si se trata de un rediseo).
              .col-md-4
                  img(src="@/assets/curso/Images/tema1/img_16.svg", alt= "")
            .row.align-items-center.justify-content-center
              .col-md-12.mb-4.mb-md-0
                  h3.mb-3 Agrupe las tarjetas
                  p.mb-3 En seguida, se deben establecer relaciones entre los tems o tpicos inventariados, para decidir cules se deben agrupar en la arquitectura dentro de las distintas secciones o mens, lo que a nivel tcnico se conoce como categoras. Aqu, la clave es descubrir dnde esperan los usuarios encontrar los tems cuando estn navegando, con el fin de llegar a su objetivo deseado.

                  p.mb-3 La diseadora Cris Busquets, en su artculo Arquitectura de la informacin: qu es y cmo hacerlo, describe el siguiente ejemplo: En una tienda <i>online</i> de muebles y productos para el hogar, cmo preferirn los usuarios navegar para encontrar los productos que buscan? querrn un acceso por tipos de muebles (camas, mesas, sillas, armarios) por uso y funciones (descansar, trabajar, cocinar)  por habitacin (dormitorio, saln, despacho, cocina)  por un buscador o por todas esas formas? Una buena tcnica para contestar a estas preguntas es el <em>Card Sorting</em>, o realizar entrevistas o encuestas a los usuarios para comprender cmo y por qu utilizan ese producto determinado. (Busquets, 2020)
              .col-lg-6
